import { 
  Timeline,
  TimelineItem,
  TimelineSeparator,
  TimelineConnector,
  TimelineContent,
  TimelineDot,
  TimelineOppositeContent
} from "@mui/lab";
import { Card, CardContent, CardMedia, Typography, useMediaQuery } from "@mui/material";
import { useRef } from "react";
import { education } from 'data/constants';

const EducationTimelineComponent = () => {
  const edu = useRef(null);
  const small = useMediaQuery('(max-width: 600px)');
  const timelinePosition = small ? '' : 'alternate';
  return (
    <Timeline position={timelinePosition} ref={edu}>
      <Typography variant='h4' component='div' id='edu' className="text-darkblue abeezee" textAlign={'center'} sx={{my:2}}>Education</Typography>
      {education.map((edu) => {
        return (
          <TimelineItem key={edu.bgColor}>
            <TimelineOppositeContent>
              <Card sx={{ background: `${edu.bgColor}`, mb: 3, mt: 1, mb: 3, mt: 1 }}>
                <CardMedia 
                  component='img' 
                  image={edu.img}
                />
                <CardContent>
                  <Typography variant='h6' className="text-darkblue">{edu.name}</Typography>
                  <Typography variant='caption'>{edu.degree}</Typography>
                  <Typography variant='body2' sx={{mt:1}}>{edu.caption}</Typography>
                </CardContent>
              </Card>
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot className="bg-darkblue" />
              <TimelineConnector className="bg-darkblue" />
            </TimelineSeparator>
            {!small && <TimelineContent />}
            {small && <TimelineContent sx={{display:'none'}} />}
          </TimelineItem>
        )
      })}
    </Timeline>
  )
}

export default EducationTimelineComponent;