import { timeline } from "data/constants";
import TimelineIcon from '@mui/icons-material/Timeline';
import { useState } from "react";
import { Box, Backdrop, SpeedDial, SpeedDialAction, SpeedDialIcon } from "@mui/material";

const TimelineSpeedDialComponent = () => {
  const [open, setOpen] = useState(false);
  const [height, setHeight] = useState(0);
  const [showSD, setShowSD] = useState(false);
  const handleTimelineOpen = () => setOpen(true);
  const handleTimelineClose = (a) => {
    setOpen(false);
    window.scrollTo(0, document.getElementById(a).offsetTop);
  }
  
  window.onscroll = () => {
    window.pageYOffset > document.getElementById('about').offsetTop ? setShowSD(true) : setShowSD(false);
  }

  
  return (
    <Box>
      {showSD && (<Box sx={{ height: '100vh', transform: 'translateZ(0px)', flexGrow: 1, position: 'fixed', bottom: 0, right: 0, zIndex: 1 }}>
        <Backdrop open={open} />
        <SpeedDial
          ariaLabel="Timeline Speeddail"
          sx={{ position: 'absolute', bottom: 16, right: 16 }}
          icon={<TimelineIcon />}
          onClose={handleTimelineClose}
          onOpen={handleTimelineOpen}
          open={open}
        >
          {timeline.map((list) => <SpeedDialAction key={list.name} icon={list.icon} tooltipTitle={list.name} tooltipOpen onClick={() => handleTimelineClose(`${list.param}`)} />)}
        </SpeedDial>
      </Box>)}
    </Box>
  )
}

export default TimelineSpeedDialComponent;